.game__button {
  cursor: pointer;
  z-index: 0;
  text-transform: uppercase;
  background: #fff;
  border: none;
  border-radius: 16px;
  outline: none;
  flex: 1;
  height: 100px;
  position: relative;
}

.game__button:before {
  content: "";
  z-index: -1;
  filter: blur(2px);
  opacity: 0;
  background: linear-gradient(45deg, #eefa03, #fff951, #ffc300, #ca4122, #faa019, #eefa03) 0 0 / 200%;
  border-radius: 16px;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  transition: opacity .3s ease-in-out;
  animation: 20s linear infinite glowing;
  position: absolute;
  top: -4px;
  left: -4px;
}

.game__button:active {
  color: #000;
}

.game__button:active:after {
  background: #f2f2f2;
}

.game__button:hover:before {
  opacity: 1;
}

.game__button:after {
  z-index: -1;
  content: "";
  background: #fff;
  border-radius: 16px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

