@use 'vars';

.footer {
  width: 100%;

  .game-timer {
    margin: 18px 0 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;

    &__wand-wrapper {
      width: 100%;
      display: flex;
      align-items: center;
    }

    &__wand {
      height: 26px;
      width: 90%;
      background: url('../assets/img/burn.png');
      background-size: cover;
      background-position: -7px;
      border-radius: 0px 8px 8px 0;

      &-inner {
        width: 100%;
        height: 100%;
        box-shadow:
          10px -5px 15px #ff8a00,
          10px 5px 15px #8e22c2,
          inset 0px 5px 4px #ff5c00;
        border-radius: inherit;
        background: vars.$wand-color;
        animation: shrinkWand linear forwards;
        animation-play-state: paused;
      }
    }
  }
}

@keyframes shrinkWand {
  from {
    width: 100%;
  }
  to {
    width: 0%;
  }
}
