@use 'vars';
@use 'mainMenu';
@use 'wand';
@use 'modalStyles';
@use 'timerText';
@use 'defaultButton';
@use 'screenModifiers';
@use 'quitGame';
@use 'hallOfFameContent';
@use 'loadingScreen';

* {
  margin: 0;
  padding: 0;
  font-family: 'Bitter', serif;
  box-sizing: border-box;
  font-size: vars.$primary-font-size;
}

body {
  background-size: cover;
  background-image: url('../assets/img/QuizBackground.png');
}

.game-wrapper {
  height: 100vh;
  width: 1400px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 4fr 1fr;
  margin: 0 auto;

  .header {
    grid-column: 1/3;
    grid-row: 1/2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;

    &__game {
      &-logo {
        width: 25%;
        min-height: 70%;
        display: flex;
        justify-content: center;
      }

      &-img {
        width: 100%;
        height: auto;
      }

      &-nav {
        display: flex;
        justify-content: space-between;
        width: 67%;
        height: 70%;
      }
    }
  }

  .game {
    grid-column: 2/3;
    grid-row: 2/3;
    display: flex;
    flex-direction: column;
    gap: 24px;

    &-image {
      grid-column: 1/2;
      grid-row: 2/3;
      display: flex;
      justify-content: space-between;
      flex-direction: column;

      &__content {
        &-image {
          width: 350px;
          height: 451px;
          object-position: top;
          object-fit: cover;
          border: 12px solid rgba(173, 25, 132, 0.26);
          filter: drop-shadow(13px 17px 11px rgba(0, 0, 0, 0.5));
          border-radius: vars.$default-radius;
        }
      }
    }
    &__mode {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      margin: 0 0 12px 0;
      width: 100%;
      height: vars.$button-height;
      color: black;
      font-weight: bold;
      font-size: 48px;
      background-color: white;
      border-radius: vars.$default-radius;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

      &-rules {
        font-size: 1.5rem;
        width: 100%;
        height: 100%;
        background-color: #fff;
        border-radius: vars.$default-radius;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        flex-grow: 1;

        &-answrBtn {
          cursor:
            url('../assets/img/cursor_wand.png') 2 2,
            pointer;
          border: none;
          border-radius: 16px;
          width: 48%;
          height: vars.$button-height;
          background-color: #fff;
          box-shadow:
            0px 4px 4px rgba(0, 0, 0, 0.25),
            inset 0px 4px 4px rgba(0, 0, 0, 0.25);

          &:nth-child(even) {
            margin: 0 0 0 25px;
          }

          &-false {
            &:focus {
              background-color: #e33336;
              font-weight: 500;
            }
          }

          &-correct {
            &:focus {
              background-color: green;
              font-weight: 500;
            }
          }
        }

        &-header {
          position: relative;
          left: 50%;
          transform: translateX(-50%);
          padding: 20px 0 8px;
          font-size: 0.6em;
          font-weight: 700;
          text-transform: capitalize;

          .rules-icon {
            position: absolute;
            left: -70px;
            font-size: 1.2em;
            margin-right: 50px;
          }
        }

        &-text {
          font-size: 0.5em;
          padding: 20px 25px 25px;
          line-height: 1.2;
        }

        &-init {
          font-size: 31px;
          padding: 20px 25px 25px;
          line-height: 1.2;
        }
      }
    }
    &__btns {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      gap: 24px;
    }
  }

  .footer {
    grid-column: 1/3;
    grid-row: 3/4;
  }

  .custom-cursor {
    cursor:
      url('../assets/img/cursor_wand.png') 2 2,
      pointer;
  }
}
